import { Button, Card, Typography, Grid, Box, TextField, Paper, Divider, Stack, isMuiElement, Select, MenuItem, IconButton, Alert } from '@mui/material'
import React, { useEffect, useState } from 'react'
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import { styled, alpha } from '@mui/material/styles';
import Modal from '@mui/material/Modal';
import { useNavigate } from 'react-router';
import guruji from "../../images/mountains.jpg"
import {  JoinEventApi, getJoinGroup, getProfileApi } from '../../API/Photographer';
import Loader from '../component/loader';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import '../../css/Home.css';
import RangeDatePicker from '../component/DatePicker';
import { SearchField } from '../component/SerachField';

export default function JoinedAlbum() {
  const [open, setOpen] = React.useState(false);
  const [isCreate, setIsCreate] = React.useState(false);
  const [geteventName, setGetEventName] = useState([]);
  const [getJoinName, setGetJoinEventName] = useState([]);
  const [eventName, setEventName] = useState('');
  const [eventDate, setEventDate] = useState('');
  const [eventProfile, setEventProfile] = useState('');
  const [error, setError] = useState('')
  const [load, setLoad] = useState(false);
  const [group, setGroup] = useState('Select')
  const [joinGroup, setJoinGroup] = useState('')
  const [status, setStatus] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const navigate = useNavigate()
  const [copiedMap, setCopiedMap] = useState({});
  const theme = useTheme();
  const isMobileOrTablet = useMediaQuery(theme.breakpoints.down('sm'));
const [grouping, setGrouping]=useState("");
const [searchValue, setSearchValue] = useState('');
  function handlegroup(data){
    console.log(data);
    setGrouping(data);
  }
   const fileHandle=(event)=>{
     setEventProfile(event?.target?.files[0])

   }

//----------------------Date filter ------------------//
const onDate=(data)=>{
console.log("fsdfgfdgdfgdfgdf",data);
const dat= data.split("#")
console.log("hhh",dat);
const dates = {
  startDate:dat[0],
  endDate:dat[1]
}
getProfile(dates);
}
// startDate=2024-03-11&endDate=2024-03-11
//----------------End Date filter----------------------//
 

  const handleCopyFallback = (text) => {
    const input = document.createElement('textarea');
    input.value = text;
    document.body.appendChild(input);
    input.select();
    document.execCommand('copy');
    document.body.removeChild(input);
  };


  const handleCopylink = async (value, index) => {
    try {
      const referralLink = value;
      if (navigator.clipboard && navigator.clipboard.writeText) {
        await navigator.clipboard.writeText(referralLink);
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: true
        }));
      } else {
        handleCopyFallback(referralLink);
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: true
        }));
      }
    } catch (error) {
      console.error("Error copying text: ", error);
    } finally {
      setTimeout(() => {
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: false
        }));
      }, 2000);
    }
  };



  const getProfile = async (data) => {
    const response = await getProfileApi()
    console.log("data",data);
    let datas = data
    console.log("response123", response?.data?.data?.Events)
    getJoinEvents(response?.data?.data?.Events,datas)
  }

  const handleEvents = async () => {
    console.log("joinGroup", joinGroup)
    if (joinGroup === "") {
      setError("Album code is required")
    }

    else {
      const data = {
        eventCode: joinGroup
      }

      const response = await JoinEventApi(data)
       console.log("response", response)
       console.log("response", response?.data?.Message)
       setStatus(response?.data?.ErrorCode)
      setErrorMessage(response?.data?.Message)
      if (response?.data?.ErrorCode === 0) {
        getProfile()

        setTimeout(()=>{
          setOpen(false);
          setErrorMessage('')
        setJoinGroup('')
        
        },2000)
         
        
        // getProfile()
      }

    }
  }

  const handleChange = (e) => {

    setGroup(e.target.value)
    setError('')
    setErrorMessage("");
  }
 
  const handleOpenForJoin = () => {
    setIsCreate(false)
    setOpen(true);

  }
  const handleClose = () => {
    setOpen(false);
    setIsCreate(false)
    setJoinGroup('')
    setError('')
    setGroup('Select')
    setErrorMessage('')
  }

  const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    borderColor: '#8CBAE8', // Set border color to black
    borderWidth: 1, // Set border width
    borderStyle: 'solid', // Set border style
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  }));


  const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }));

  const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'black',
    borderColor: 'black',
    '& .MuiInputBase-input': {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)})`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('md')]: {
        width: '20ch',
      },
    },
  }));

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
  };


  const UserList = (item) => {
    const eventProps = item;
    navigate("/upload", { state: { eventProps } });
  };


  useEffect(() => {
    getProfile()
   
     getJoinEvents()

  }, [])

  const getJoinEvents = async (item,datas) => {
    console.log("item", item)
    const data = item?.map(item => item.eventCode)

    console.log("data123", datas)
    const response = await getJoinGroup(data,datas)
    console.log("response256", response)
    
    setGetJoinEventName(response?.data?.data || [])
  }


const payment=()=>{
  navigate('/subscription')
  
}

//---------------sticky scroll Header -----------------------//
const [isScrolling, setIsScrolling] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleScroll = () => {
    setIsScrolling(true);
    let scrollTimeout; 
    clearTimeout(scrollTimeout);
    scrollTimeout = setTimeout(() => {
      setIsScrolling(false);
    }, 200);
  };
  //-----------------------------------------------------------------------//
  //-----------------search onChange ----------------------------//

const handleSearchChange = (data) => {
  console.log("data",data)
 setSearchValue(data);
   const datas = {
    startDate: "",
    endDate: "",
    search: data
  };
  console.log(data);
  

  getProfile(datas);

};


//------------------------------------------------------------//

  return (
    <div>
      {load ? <Loader open={load} /> : null}
    
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        {
          isCreate ?
           null
            :
            <>
              <Box sx={style}>
              {
                  errorMessage? <Alert severity={status === 0 ? "success" : "error"}>{errorMessage}</Alert>:null
                }
             

                <Typography style={{ fontSize: '18px', color: 'black' }} >Enter Album Code *</Typography>
                <TextField placeholder='Enter Album Code' style={{ width: "100%" }} value={joinGroup} onChange={(e) => { setJoinGroup(e.target.value); setError(''); setErrorMessage('')}} />
                {error === "Album code is required" ? <Typography style={{ color: 'red', fontSize: '16px', textAlign: "left" }}>{error}</Typography> : null}

                <Button variant='contained' fullWidth style={{ marginTop: 20 }} onClick={handleEvents}>Join</Button >
              </Box>
            </>
        }

      </Modal>
      <div style={{ position: 'sticky',top:"70px",zIndex:'999', width: '100%', transition: 'top 0.3s', top: isScrolling ? '-100px' : '70px' }}>
        <Card style={{ marginTop: 10, width: '100%' }} elevation={3}>
        {
          isMobileOrTablet ?
            <Grid container spacing={2} sx={{padding:'10px'}}>
              <Grid item xs={12} sm={12}>
                <Button variant='outlined' onClick={handleOpenForJoin} fullWidth={isMobileOrTablet ? true : null} >
                      Join Album
                    </Button>
              </Grid>
              <Grid item xs={12} sm={12}>
              <RangeDatePicker filterDate={onDate}/>
              </Grid>
 
              <Grid item xs={12} sm={12}>
              <SearchField width={'100%'} searchProps={handleSearchChange} placeholder="Search, Album name" />
              </Grid>
            </Grid>
            :
            <div style={{ display: 'flex', justifyContent: 'space-between', padding: 20 }}>
              <div>
                <Grid container spacing={2}>

                  <Grid item xs={12} sm={12} >

                    <Button variant='outlined' onClick={handleOpenForJoin} fullWidth={isMobileOrTablet ? true : null} style={{ marginLeft: 20 }}>
                      Join Album
                    </Button>

                  </Grid>
                </Grid>
              </div>
              <div>
                <Stack direction={"row"}>
               
             <RangeDatePicker filterDate={onDate}/>

             <SearchField width={'100%'} searchProps={handleSearchChange} placeholder="Search, Album name"/>
                </Stack>
              
              </div>
            </div>
        }


</Card>
</div>

       
         {/* { getJoinName?.length >0 ?
          <Typography style={{ marginTop: 20, fontWeight: "bold" }} variant='h5'>Joined Albums</Typography>:null} */}
          <div style={{marginTop:'10px'}}
          >
        <Grid container spacing={2} marginTop={isMobileOrTablet ? 1 : null} >
      
          {getJoinName?.map((item, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Box onClick={() => UserList(item)} >
                <Card style={{ width: '100%' }} elevation={3} >
                  <img src={ item.imageUrl? item.imageUrl:guruji} style={{ height: 250, width: '100%' }} alt="Event" />
                </Card>

              </Box>
              <Stack direction={"row"} gap={1} >
                <Stack >
                  <Typography style={{ textAlign: 'left', fontWeight: "bold", marginTop: 2 }} variant='h6'>
                    {item?.name}
                  </Typography>
              
                </Stack>
                <Stack>
                <Typography style={{ textAlign: 'left', fontWeight: "bold", marginTop: 2 }} variant='h6'>
                    {item?.eventDate}
                  </Typography>
                </Stack>

              </Stack>
            </Grid>
          ))}
        </Grid>
        </div>
    </div>

  )
}





