const [fileExist,setFileExist]=useState();
const fileCheck=async(data,selectedFiles)=>{
  try{
    const formData = new FormData();
selectedFiles.forEach(file => {
  formData.append("files", file)
});
const res = await fileCheking(data,eventData?.eventCode,formData);
setloderForModel(false);

const fileExist = res.data.data;

const newImageFileNames = fileExist?.filter(item => item.exists === "New image").map(item => item.fileName);



const filteredSelectedFiles = selectedFiles?.filter(file => newImageFileNames?.includes(file.name));

setSelectedFile(filteredSelectedFiles);

setFileExist(res.data.data)
  }catch(err){
console.log(err);
  }
}
  const fileChange = (event) => {

    console.log("event.target.files",event.target.files)
    setloderForModel(true);

    //  setStatus('')
    setErrorMessage('');
    const selectedFiles = Array.from(event.target.files)?.filter(file => {
        const fileType = file.type;
        const filedetail = fileType.split("/")[0]
        return filedetail === 'image';
    });
 
    if(selectedFiles){
      const selectedFileNames = Array.from(selectedFiles)
      .map(file => file.name);

      fileCheck(selectedFileNames,selectedFiles)
    }

};



// console.log("fileExistfileExistfileExist",fileExist);
  useEffect(() => {
    const script = document.createElement('script');
    script.src = "https://apis.google.com/js/api.js";
    script.async = true;
    script.onload = () => {
      //console.log("Test", window.gapi); // Check if gapi is defined
    };
    document.body.appendChild(script);
    return () => document.body.removeChild(script);
  }, []);



