import React, { useState, useEffect, useRef } from 'react';
import { Input } from '@mui/material';

const RangeDatePicker = ({ filterDate,width }) => {
  const [startDate, setStartDate] = useState('');
  const [endDate, setEndDate] = useState('');
  const [pickerOpen, setPickerOpen] = useState(false);
  const datePickerRef = useRef();

  useEffect(() => {
    
    const handleClickOutside = (event) => {
      if (datePickerRef.current && !datePickerRef.current.contains(event.target)) {
        setPickerOpen(false);
      }
    };

  
    document.addEventListener('mousedown', handleClickOutside);

    
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const handleStartDateChange = (event) => {
    const newStartDate = event.target.value;
    console.log("newStartDate",newStartDate);
    setStartDate(newStartDate);
    if (endDate && new Date(newStartDate) > new Date(endDate)) {
      setEndDate('');
    }
    if (newStartDate && endDate) {
        const da = newStartDate + "#" + endDate;
        filterDate(da);
      setPickerOpen(false);
    }
  };

  const handleEndDateChange = (event) => {
    const newEndDate = event.target.value;
    if (!startDate || new Date(newEndDate) >= new Date(startDate)) {
      setEndDate(newEndDate);
    }
    if (startDate && newEndDate) {
      const da = startDate + "#" + newEndDate;
      filterDate(da);
      setPickerOpen(false);
    }
  };

  const togglePicker = () => {
    setPickerOpen(!pickerOpen);
  };

  return (
    <div ref={datePickerRef} style={{width:width?width:"100%"}}>
      <Input 
        type="text"
        placeholder="Filter Album"
        onClick={togglePicker}
        readOnly
        
        style={{width:'90%',height:'40px' }}
        value={startDate && endDate ? `${startDate} - ${endDate}` : ''}
      />
      {pickerOpen && (
        <div>
          <input
            type="date"
            value={startDate}
            onChange={handleStartDateChange}
            min="1970-01-01"
            max={endDate ? endDate : ''}
          />
          <input
            type="date"
            value={endDate}
            onChange={handleEndDateChange}
            min={startDate ? startDate : '1970-01-01'}
            max={new Date().toISOString().split('T')[0]}
          />
        </div>
      )}
    </div>
  );
};

export default RangeDatePicker;

