import { Button, Card, Typography, Grid, Box, TextField, Paper, Divider, Stack, isMuiElement, Select, MenuItem, IconButton, Alert } from '@mui/material'
import React, { useEffect, useRef, useState } from 'react'
import SearchIcon from '@mui/icons-material/Search';
import InputBase from '@mui/material/InputBase';
import { styled, alpha } from '@mui/material/styles';
import Modal from '@mui/material/Modal';
import { useNavigate } from 'react-router';
import guruji from "../../images/mountains.jpg"
import { CreateEvent, EditEvent, EditEvents, GetEventApi, JoinEventApi, deleteMyEvent,  getProfileApi } from '../../API/Photographer';

 import Loader from '../component/loader';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';
import '../../css/Home.css';
// import DatePicker from '../component/DatePicker';
import { Delete, Edit } from '@mui/icons-material';
import DeleteModal from '../component/deleteModal';
import { SearchField } from '../component/SerachField';
import RangeDatePicker from '../component/DatePicker';
export default function Groups() {
  const [open, setOpen] = React.useState(false);
  const [isCreate, setIsCreate] = React.useState(false);
  const [geteventName, setGetEventName] = useState([]);
  const [getJoinName, setGetJoinEventName] = useState([]);
  const [eventName, setEventName] = useState('');
  const [editedData, setEditedData] = useState('');
  const photographerEmail=localStorage.getItem("email")
  const [eventDate, setEventDate] = useState('');
  const [eventProfile, setEventProfile] = useState('');
  const [error, setError] = useState('')
  const [load, setLoad] = useState(false);
  const [isEditData, setIsEdit] = useState(false);
  const [group, setGroup] = useState('Select')
  const [joinGroup, setJoinGroup] = useState('')
  const [status, setStatus] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const navigate = useNavigate()
  const [copiedMap, setCopiedMap] = useState({});
  const theme = useTheme();
  const isMobileOrTablet = useMediaQuery(theme.breakpoints.down('sm'));
  const [filterDate,setFilterDate]=useState('');
  const [opens, setOpens] = useState(false);
const [deleteItem,setDeleteItem]=useState('');
const [grouping, setGrouping]=useState("");
const [searchValue, setSearchValue] = useState('');

  function handlegroup(data){
    console.log(data);
    setGrouping(data);
  }
   const fileHandle=(event)=>{
    setErrorMessage("");
    const file =event?.target?.files[0]
    //  setEventProfile(event?.target?.files[0])
    console.log("file",file)
     if (file) {
      const fileType = file.type;
      
      if (fileType.startsWith('image/')) {
          setEventProfile(file);
      } else {
        setEventProfile(file);
        setErrorMessage('Please select an image file.');
      }
  }

   }

console.log("groupe",group)

   const editEvent=async(item)=>{
    setIsEdit(true)
    console.log(item);
    setEditedData(item)
    setEventName(item.name);
    setGroup(item?.eventType);
 
    // if(item.imageUrl){

    //   const EditedImages=item?.imageUrl?.replace(/.*\//, '')
    //   console.log("EditedImages",EditedImages)
       
    //   setEventProfile(EditedImages)
    // }
  
    const date = item.eventDate
    const parts = date.split("-");
  const year = parts[0];
  const month = parts[1];
  const day = parts[2];
  const formattedDate = `${year}-${month}-${day}`;
  
  console.log(formattedDate);
    setEventDate(formattedDate)
    setOpen(true);
  }



  const handleEventSubmit = async () => {
    const fileType = eventProfile.type;
    console.log("eventProfile;",eventProfile)
    console.log("fileType",fileType)
    const formData= new FormData();
    try {
      if (group === "Select") {
        setErrorMessage("Album type is required")
      }
      else if (eventName === "") {
        setErrorMessage("Album Name is required")
      } else if (eventDate === "") {
        setErrorMessage("Event Date is required");
      } else if(fileType && !fileType?.startsWith('image/') ){
        setErrorMessage("Please select an image file.");
      }
       else {
    
        setError('')
        setLoad(true);
        let response=""
        
        if(isEditData){
              const userData = {
                name: eventName,
                eventType: group,
                eventDate: eventDate,
                eventCode:editedData.eventCode
              }
              console.log("userData2",userData)
              formData.append("data",JSON.stringify(userData))
              if(eventProfile){
                formData.append("file",eventProfile,eventProfile?.name)
              }
              console.log("Vittal")
              response = await EditEvents(formData);

              console.log("response",response)
        }
        else{



          const userData = {
            name: eventName,
            eventType: group,
            eventDate: eventDate,
            photographerEmail
          }
          console.log("userData2",userData)
          formData.append("data",JSON.stringify(userData))
          if(eventProfile){
            formData.append("file",eventProfile,eventProfile?.name)
          }
          response = await CreateEvent(formData);
        }
         
        setLoad(false);
        setErrorMessage(response?.data?.Message)
        setStatus(response?.status)
        if (response?.status === 200) {
          setTimeout(() => {
                 setOpen(false);
                 setErrorMessage("");
                 setStatus("")
        setIsCreate(false)
          }, 2000)
          setEventProfile('')
          
        } else {
          setErrorMessage(response?.response?.data?.Message);
        
        }
        setEventDate('');
        setEventName('');
        getEvent()
   
      }
    } catch (err) {
      console.log(err);
    }

  }

  const handleCopyFallback = (text) => {
    const input = document.createElement('textarea');
    input.value = text;
    document.body.appendChild(input);
    input.select();
    document.execCommand('copy');
    document.body.removeChild(input);
  };


  const handleCopylink = async (value, index) => {
    try {
      const referralLink = value;
      if (navigator.clipboard && navigator.clipboard.writeText) {
        await navigator.clipboard.writeText(referralLink);
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: true
        }));
      } else {
        handleCopyFallback(referralLink);
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: true
        }));
      }
    } catch (error) {
      console.error("Error copying text: ", error);
    } finally {
      setTimeout(() => {
        setCopiedMap(prevState => ({
          ...prevState,
          [index]: false
        }));
      }, 2000);
    }
  };



  const getProfile = async () => {
    const response = await getProfileApi()

    console.log("response123", response?.data?.data?.Events)
    // getJoinEvents(response?.data?.data?.Events)
  }
  const handleEvents = async () => {
    console.log("joinGroup", joinGroup)
    if (joinGroup === "") {
      setError("Album code is required")
    }

    else {
      const data = {
        eventCode: joinGroup
      }

      const response = await JoinEventApi(data)
       console.log("response", response)
       console.log("response", response?.data?.Message)
       setStatus(response?.data?.ErrorCode)
      setErrorMessage(response?.data?.Message)
      if (response?.data?.ErrorCode === 0) {
        getProfile()

        setTimeout(()=>{
          setOpen(false);
          setErrorMessage('')
        setJoinGroup('')
        
        },2000)
         
        
        // getProfile()
      }

    }
  }

  const handleChange = (e) => {

    setGroup(e.target.value)
    setError('')
    setErrorMessage("");
  }
  const handleOpenForCreate = () => {
    // setIsCreate(true)
    setEventDate('');
    setEventName('');
    setOpen(true);
    setIsCreate(true)

  }

  const handleOpenForJoin = () => {
    setIsCreate(false)
    setOpen(true);

  }
  const handleClose = () => {
    setOpen(false);
    setIsCreate(false)
    setJoinGroup('')
    setError('')
    setGroup('Select')
    setErrorMessage('')
    setStatus('')
    setIsEdit(false)
  }

  const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    borderColor: '#8CBAE8', // Set border color to black
    borderWidth: 1, // Set border width
    borderStyle: 'solid', // Set border style
    '&:hover': {
      backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
      marginLeft: theme.spacing(3),
      width: 'auto',
    },
  }));


  const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  }));

  const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'black',
    borderColor: 'black',
    '& .MuiInputBase-input': {
      padding: theme.spacing(1, 1, 1, 0),
      // vertical padding + font size from searchIcon
      paddingLeft: `calc(1em + ${theme.spacing(4)})`,
      transition: theme.transitions.create('width'),
      width: '100%',
      [theme.breakpoints.up('md')]: {
        width: '20ch',
      },
    },
  }));

  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
  };


  const UserList = (item) => {
    const eventProps = item;
    navigate("/upload", { state: { eventProps } });
  };


  useEffect(() => {
    getProfile()
    getEvent()
    // getJoinEvents()

  }, [])
  const getEvent = async (dates) => {
    try {
      const response = await GetEventApi(dates)
      console.log("hjhjh",response);
      setGetEventName(response?.data?.data || [])
    } catch (error) {

    }
  }

  // const getJoinEvents = async (item) => {
  //   console.log("item", item)
  //   const data = item?.map(item => item.eventCode)

  //   console.log("data123", data)
  //   const response = await getJoinGroup(data)
  //   console.log("response256", response)
    
  //   setGetJoinEventName(response?.data?.data || [])
  // }

const publicGroup =()=>{
  navigate('/publicGroup')
  
}

const payment=()=>{
  navigate('/subscription')
  
}
//----------------------Date filter ------------------//
const onDate=(data)=>{
  console.log("fsdfgfdgdfgdfgdf",data);
  const dat= data.split("#")
  console.log("hhh",dat);
  const dates = {
    startDate:dat[0],
    endDate:dat[1],
    search:""
  }

  getEvent(dates);
  }
  // startDate=2024-03-11&endDate=2024-03-11
  //----------------End Date filter----------------------//
//-----------------Delete MY events--------------------//
const DleteMyEvents=async(item)=>{
 
     try{
          const response = await deleteMyEvent(item);
          console.log("fsdfsdfsdfsdf",response);
          setEventProfile('');
          setEventName('');
          getEvent();
          getProfile();
          setDeleteItem('');
     }catch(err){
      console.log(err);
     }
}
//---------------end delete event---------------------------//
//----------------Edit My Event-----------------------------//


//----------------------------------------------------------//

const handleDeleteOpen = (item) => {
  setOpens(true);
  // DleteMyEvents
  setDeleteItem(item);
};

const handledeleteclose = () => {
  setDeleteItem('')
  setOpens(false);
};

const handleAgree = () =>{
  DleteMyEvents(deleteItem)
  setOpens(false);
}

//-------------------------------------------------------------//
//-----------------search onChange ----------------------------//

// useEffect(() => {
//   const data = {
//     startDate: "",
//     endDate: "",
//     search: searchValue
//   };
//   getEvent(data);
// }, [searchValue]);

const handleSearchChange = (data) => {
  console.log("data",data)
 setSearchValue(data);
   const datas = {
    startDate: "",
    endDate: "",
    search: data
  };
  console.log(data);
  

  getEvent(datas);

};


//------------------------------------------------------------//
//---------------sticky scroll Header -----------------------//
const [isScrolling, setIsScrolling] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const handleScroll = () => {
    setIsScrolling(true);
    let scrollTimeout; 
    clearTimeout(scrollTimeout);
    scrollTimeout = setTimeout(() => {
      setIsScrolling(false);
    }, 200);
  };
  //-----------------------------------------------------------------------//


  console.log("status",status)
  return (
    <div>
      {load ? <Loader open={load} /> : null}
      <DeleteModal open={opens} handleClose={handledeleteclose} handleAgrreDelete={handleAgree}/>
   
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        
         
              <Box sx={style}>
                {
                  errorMessage? <Alert severity={status === 200 ? "success" : "error"}>{errorMessage}</Alert>:null
                }

                <Stack spacing={1} style={{ marginBottom: '6px' }}>
                  <Typography style={{ fontSize: '18px', color: 'black' }} >Album Type *</Typography>
                  <Select
                    value={group}
                    onChange={handleChange}
                    
                  >
                    <MenuItem value={"Select"}>Select</MenuItem>
                    <MenuItem value={"Public"}>Public</MenuItem>
                    <MenuItem value={"Private"}>Private</MenuItem>

                  </Select>
                  {error === "Album type is required" ? <Typography style={{ color: 'red', fontSize: '16px', textAlign: "left" }}>{error}</Typography> : null}
                </Stack>
                <Stack spacing={1} style={{ marginBottom: '6px' }}>
                  <Typography style={{ fontSize: '18px', color: 'black' }} >Album Name *</Typography>
                  <TextField placeholder='Enter Album Name' value={eventName} onChange={(e) => { setEventName(e.target.value); setError(''); setErrorMessage(""); }} style={{ width: "100%" }} />
                  {error === "Album Name is required" ? <Typography style={{ color: 'red', fontSize: '16px', textAlign: "left" }}>{error}</Typography> : null}
                </Stack>
                <Stack spacing={1}>
                  <Typography style={{ fontSize: '18px', color: 'black', textAlign: "left" }} >Event Date *</Typography>
                  <TextField
                    placeholder='Select Event Date'
                    type='date' // Use 'date' instead of 'Date'
                    value={eventDate} 
                    onChange={(e) => {
                      console.log(e.target.value);
                      setEventDate(e.target.value);
                      setError('');
                      setErrorMessage('');
                    }}
                    style={{ width: "100%" }}
                  />
                  {error === "Event Date is required" ? <Typography style={{ color: 'red', fontSize: '16px', textAlign: "left" }}>{error}</Typography> : null}
                </Stack>
                <Stack spacing={1}>
                <Typography style={{ fontSize: '18px', color: 'black', textAlign: "left" }} >Album Profile Image</Typography>
                <TextField style={{ width: "100%" }} type='file' name='file' onChange={fileHandle}  />

                {
                  editedData?.imageUrl && <Typography>Profile image {editedData?.imageUrl.replace(/.*\//, '')}</Typography>
                }
                </Stack>
                
                <Button variant='contained' fullWidth style={{ marginTop: 20 }} onClick={handleEventSubmit}>{isEditData ?"Update":"Create"}</Button >
              </Box>
        

      </Modal>
      <div style={{ position: 'sticky',top:"70px",zIndex:'999', width: '100%', transition: 'top 0.3s', top: isScrolling ? '-100px' : '70px' }}>
        <Card style={{ marginTop: 10, width: '100%' }} elevation={3}>
        {
          isMobileOrTablet ?
            <Grid container spacing={2} sx={{padding:'10px'}}>
              <Grid item xs={12} sm={12}>
                <Button variant='outlined' onClick={handleOpenForCreate} fullWidth={isMobileOrTablet ? true : null}>
                  Create Album
                </Button>
              </Grid>
              <Grid item xs={12} sm={12}>
              {/* <DatePicker width={'100%'} filterDate={onDate}/> */}
              <RangeDatePicker width={'100%'} filterDate={onDate}/>
              </Grid>
              <Grid item xs={12} sm={12}>
              <SearchField width={'100%'} searchProps={handleSearchChange} placeholder="Search, Album name"/>

              </Grid>
            </Grid>
            :
            <div style={{ display: 'flex', justifyContent: 'space-between', padding: 20 }}>
              <div>
                <Grid container spacing={2}>

                  <Grid item xs={12} sm={12} >

                    <Button variant='outlined' onClick={handleOpenForCreate}>
                      Create Album
                    </Button>

                  </Grid>
                </Grid>
              </div>
              <div>
                <Stack direction={"row"}>
               
               {/* <DatePicker width={'100%'} filterDate={onDate}/> */}
               <RangeDatePicker width={'100%'} filterDate={onDate}/>

               <SearchField width={'100%'} searchProps={handleSearchChange} placeholder="Search, Album name"/>

                </Stack>
              
              </div>
            </div>
        }
         </Card>
         </div>
        {/* {
          geteventName?.length >0 ?
          <Typography style={{ marginTop: 20, fontWeight: "bold" }} variant='h5'>My Albums</Typography>:null } */}
       
       <div style={{marginTop:'10px'}}>
        <Grid container spacing={2} marginTop={isMobileOrTablet ? 1 : null} >
          {geteventName?.map((item, index) => (
            <Grid item xs={12} sm={6} md={4} key={index}>
              <Box onClick={() => UserList(item)} >
                <Card style={{ width: '100%' }} elevation={3}>
                  {/* <img src={ item.imageUrl? item.imageUrl:guruji} style={{ height: 250, width: '100%', objectFit: 'cover', objectPosition:"top",filter: 'brightness(1.1) contrast(1.1)'}} alt="Event" /> */}
                  <img src={ item.imageUrl? item.imageUrl:guruji} style={{ height: 250, width: '100%'}} alt="Event" />

                  {/* <div style={{ display: 'flex', justifyContent: 'space-between'}}>
                <Box class="my-album-edit">
                 <Edit  onClick={(event) => {event.stopPropagation();editEvent(item);}}/>
                </Box>
                <Box class="my-album-delete">
                <Delete color='white'  onClick={(event) => {event.stopPropagation();handleDeleteOpen(item);}} />
                </Box>
              </div> */}
              <div style={{ display: 'flex', justifyContent: 'space-between'}}>
  <Box 
    className="my-album-edit"
    style={{
      transition: 'transform 0.3s ease-in-out',
    }}
  >
    <Edit 
      onClick={(event) => {event.stopPropagation(); editEvent(item);}}
      style={{
        cursor: 'pointer',
      }}
      onMouseEnter={(event) => {
        event.currentTarget.style.transform = 'scale(1.5)';
      }}
      onMouseLeave={(event) => {
        event.currentTarget.style.transform = 'scale(1)';
      }}
    />
  </Box>
  <Box 
    className="my-album-delete"
    style={{
      transition: 'transform 0.3s ease-in-out',
    }}
  >
    <Delete 
      color='white' 
      onClick={(event) => {event.stopPropagation(); handleDeleteOpen(item);}}
      style={{
        cursor: 'pointer',
      }}
      onMouseEnter={(event) => {
        event.currentTarget.style.transform = 'scale(1.5)';
      }}
      onMouseLeave={(event) => {
        event.currentTarget.style.transform = 'scale(1)';
      }}
    />
  </Box>
</div>


                </Card>
              </Box>
              <Stack direction={"row"} gap={1}>
                <Stack>
                  <Typography style={{ textAlign: 'left', fontWeight: "bold", marginTop: 2 }} variant='h6'>
                    {item?.name}
                  </Typography>
                </Stack>
                <Stack direction={"row"}>
                  <IconButton
                    onClick={() => handleCopylink(item?.eventCode, index)}
                    aria-label="Copy Email Key"
                    style={{ cursor: "pointer" }}
                  >
                    <ContentCopyIcon />
                  </IconButton>
                  <div style={{ marginTop: 10 }}>
                    {copiedMap[index] && <span style={{ color: "green" }}>Copied!</span>}
                  </div>
                </Stack>
                <Stack >
            <Typography style={{ textAlign: 'left', fontWeight: "bold", marginTop: 2 }} variant='h6'>
              {item?.eventDate}
            </Typography>
          </Stack>
              </Stack>
            </Grid>
          ))}
        </Grid>
        </div>
       
    
    </div>

  )
}





