import React, { useEffect, useState } from 'react';
import Login1 from './Login1';
import Login2 from './Login2';
import { Alert, Button, Card, Grid, Typography } from '@mui/material';
import mountanImage from "../../images/vishal.png"


import Login4 from './Login4';
import { Register, otpSend, otpVerify } from '../../API/Auth';
import { json, useNavigate, useNavigation } from 'react-router';

import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import { BackHand } from '@mui/icons-material';
import {arrowLeft} from 'react-icons-kit/fa/arrowLeft'
import {ic_keyboard_backspace_outline} from 'react-icons-kit/md/ic_keyboard_backspace_outline'
import Icon from 'react-icons-kit';


function Login() {
  const [currentStep, setCurrentStep] = useState(1);
  const [errorMessage, setErrorMessage] = useState('');
  const [userEmail, setUserEmail] = useState('');
  const [userData,setUserData]=useState('');
  const [otp, setOtp] = useState('');
  const theme = useTheme();
  const isMobileOrTablet = useMediaQuery(theme.breakpoints.down('sm'));
  const [successMessage,setSuccessMessage]=useState('');
   const navigate = useNavigate('');

   useEffect(() => {
    const handleKeyDown = (event) => {
      if (event.key === 'Enter') {
        nextStep();
      }
    };

    document.addEventListener('keydown', handleKeyDown);

    return () => {
      document.removeEventListener('keydown', handleKeyDown);
    };
  }, [currentStep]); 

  

  const Login1Data = (data) => {
    setErrorMessage('');
    setUserEmail(data);
  };

  const Login2Data = (data) => {
    setErrorMessage('');
    setOtp(data);
  };

  const Login4Data = (data) => {
    setErrorMessage('');
    setSuccessMessage('');
    setUserData(data);
  };

  const EmailSubmit = async () => {
    const emailFormat = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!userEmail) {
      setErrorMessage("Email is required");
    } else if (!emailFormat.test(userEmail)) {
      //console.log("Invalid Email format");
      setErrorMessage("Invalid Email format");
    } else {
      try {
        const data = {
          email: userEmail
        };
        const response = await otpSend(data);
        if (response.status === 201) {
          setErrorMessage("");
          setCurrentStep(currentStep + 1);
        }
      } catch (err) {
        console.error(err);
        setErrorMessage(err.message)
      }
    }
  };

  const otpSubmit =async() => {
    // try {
    //   if (!otp) {
    //     setErrorMessage("Enter Otp");
    //   } else if (otp.length !== 6) {
    //     setErrorMessage("Enter all 6 digits");
    //   }else{
    //   const data = {
    //     email: userEmail,
    //     otp:otp
    //   };

    //   const response = await otpVerify(data);

    //   console.log("responsemessage",response);
    //   console.log("responsemessage",response?.data?.data?.email);
    //   if (response.status === 200) {
    //     setErrorMessage("");
    //     if(response?.data?.userType === null ){
    //       setCurrentStep(currentStep + 1);
    //     }else{
    //       console.log("profileImage12",response?.data.data?.imageUrl)
    //       localStorage.clear();
    //       localStorage.setItem("email",response?.data?.data?.email);
    //       localStorage.setItem("accessToken",response?.data?.accessToken);
    //       localStorage.setItem("userType",response?.data?.userType);
    //       localStorage.setItem("userId",response?.data?.data?._id);
    //       localStorage.setItem("profileImage",response?.data.data?.imageUrl);
         
    //       setSuccessMessage("Logged in Successfully")
    //       setTimeout(()=>{
    //         setSuccessMessage('');
    //         navigate('/home');
    //       },2000)
    //     }
    
       
    //   }else{
    //     setErrorMessage("Wrong OTP");
    //   }
    // }
    // } catch (err) {
    //   console.error(err);
    //   setErrorMessage(err.message)
    // }
  

  };

  const submitProfile=async()=>{
    console.log("UserDetailes",userData)
    try{
    
      const emailFormat = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if(userData.name=== "" || userData.name === undefined ){
          setErrorMessage("Name is required.");
        
      }
      else if (!userData?.phone){
        setErrorMessage("Phone number is required.");
      } 
     
      else if(userData?.file===null){
        setErrorMessage("Profile photo is required.");
      }
      // else if(userData.email=== "" || userData.email === undefined){
      //   setErrorMessage("Email is required");
      // }else if(!emailFormat.test(userData.email)){
      //   setErrorMessage("Email is invalid format");
      // }
      // else if(userData?.file === null){
      //   setErrorMessage("File is required");
      // }else if(userData?.file?.type != 'image/jpeg' && userData?.file?.type != 'image/png' && userData?.file?.type != 'image/jpg'){
      //   setErrorMessage("Select is valid format image");
      // }
      else{
        const formdata= new FormData();
        const userDeatail={
          name:userData.name,
          email: userEmail,
          phone:userData.phone || "",
          userType:"user",
        }
        console.log("userDeatail",userDeatail)
        formdata.append("userData",JSON.stringify(userDeatail));
        formdata.append("file",userData.file)
         const result = await Register(formdata);
      
          console.log("result",result)
        if(result.status === 200){
        
          localStorage.clear();
          localStorage.setItem("accessToken",result?.data?.accessToken);
          localStorage.setItem("userType",result?.data?.userType);
          localStorage.setItem("userId",result?.data.data?._id);
          localStorage.setItem("email",result?.data.data?.email);
          localStorage.setItem("profileImage",result?.data.data?.imageUrl);
          setSuccessMessage("Profile Completed Successfully")
      
            setTimeout(()=>{
              setSuccessMessage('');
              navigate('/');
            },2000)
          
        }else{
          //console.log("result?.data?.error",result?.response?.data?.error);
          setSuccessMessage(result?.response?.data?.error)
        }
      
      }

    }catch(err){
      setErrorMessage(err.message)
    }
  }



  const nextStep = () => {
    if (currentStep === 1) {
      EmailSubmit();
    } else if (currentStep === 2) {
       otpSubmit();
    }else {
      submitProfile();
    }
  };

  const moveNextStep = () => {
    setCurrentStep(currentStep + 1);
  };

  const prevStep = () => {
    setCurrentStep(currentStep - 1);
    setErrorMessage("");
  };

  const renderStep = () => {
    switch (currentStep) {
      case 1:
        return <Login1 nextStep={nextStep} Login1Data={Login1Data} errorMessage={errorMessage} />;
      case 2:
        return <Login2 moveNextStep={moveNextStep} prevStep={prevStep}  Login2Data={Login2Data} errorMessage={errorMessage} userEmail={userEmail}/>;
      case 3:
        return <Login4 prevStep={prevStep} nextStep={nextStep} Login4Data={Login4Data} errorMessage={errorMessage}/>;
      default:
        return null;
    }
  };

  const renderImage = () => {
    switch (currentStep) {
      case 1:
        return <img src={mountanImage} style={{ height: 450, width: "100%" }} />;
      case 2:
        return <img src={mountanImage} style={{ height: 450, width: "100%" }} />;
      case 3:
        return <img src={mountanImage} style={{ height: 450, width: "100%" }} />;
      default:
        return null;
    }
  };

  return (
    <div>
          {/* {successMessage ? <SuccessComponent message={successMessage} /> : null} */}
 
    <div style={{ display: 'flex', justifyContent: "center", marginTop: 60 }}>
     
      <Card style={{ width: 800,  padding:isMobileOrTablet?10:'', margin:10}} elevation={5}  >

        <Grid container direction={"row"} spacing={2} >
          <Grid item md={6} lg={6} sm={12} xs={12}  >
            {renderImage()}
          </Grid>
          <Grid item md={6} xs={12} sm={12} lg={6}  >
            <div style={{ marginTop: 10, marginRight: 10 }}>
             {successMessage? <Alert severity="success">{successMessage}</Alert> :null} 
              {renderStep()}
              <Button onClick={nextStep}  fullWidth variant='outlined' style={{ marginTop: 20 }}>Continue</Button>
            </div>
         
            {
              currentStep === 1 ? null: 
            <div  style={{marginTop:10}}>
            <Icon size={35} icon={ic_keyboard_backspace_outline} onClick={prevStep}/>
              </div>
           }
         
          </Grid>
          
        </Grid>
      </Card>
    </div>
    </div>
  );
}

export default Login;

