import { InputLabel, Stack, TextField, Typography } from '@mui/material'
import React, { useState } from 'react'
import 'react-phone-input-2/lib/style.css'
export default function Login4({Login4Data,errorMessage}) {
  const [userData, setUserData] = useState({
    name: '',
    studioName: '',
    phone: '',
    studiolocation: '',
    file: null
  });

  const handleChange = (e) => {
    const { name, value, type } = e.target;
   
    if (type === 'file') {
       const file = e.target.files[0];
       if (file && (file.type === 'image/jpeg' || file.type === 'image/png')) {
         setUserData({
           ...userData,
           [name]: file
         });
         Login4Data({ ...userData, [name]: file });
       } else {
         alert('Please select a JPG or PNG file.');
       }
    } else if (name === 'phone') {
       // Filter out non-digit characters
       const digitsOnly = value.replace(/\D/g, '');
       setUserData({
         ...userData,
         [name]: digitsOnly
       });
       Login4Data({ ...userData, [name]: digitsOnly });
    } else {
       setUserData({
         ...userData,
         [name]: value
       });
       Login4Data({ ...userData, [name]: value });
    }
   };
   
   


  return (
    <>
        <Stack spacing={1} style={{marginTop:50}}>
          <div>
        
        <TextField  style={{width:"100%"}}  placeholder='Enter your full name *' name='name' value={userData.name} onChange={handleChange}/>
        {
           errorMessage==="Name is required."? <Typography style={{color:"red"}} textAlign={"left"}>{errorMessage}</Typography>: null
        } 
        </div>

        <div>
         
        <TextField  style={{width:"100%"}} placeholder='Enter your phone number *' name='phone' value={userData.phone} onChange={handleChange}/>
        {
            errorMessage==="Phone number is required." ? <Typography style={{color:"red"}} textAlign={"left"}>{errorMessage}</Typography>: null
        }
        </div>
{/* 
        <div> 
        <TextField  style={{width:"100%"}} placeholder='Enter photography studio name *'name='studioName' value={userData.studioName} onChange={handleChange} />
        {
          errorMessage==="Photography studio name is required." ? <Typography style={{color:"red"}} textAlign={"left"}>{errorMessage}</Typography>: null
        }
        </div> */}
   
        {/* <div>
         
        <TextField  style={{width:"100%"}}  placeholder='Enter photography studio location *'name='studiolocation' value={userData.studiolocation} onChange={handleChange}/>
        {
           errorMessage==="Photography location is required." ? <Typography style={{color:"red"}} textAlign={"left"}>{errorMessage}</Typography>: null
        } 

        </div> */}

        <div>
       
       <Typography>Choose your profile photo</Typography>
        <TextField style={{ width: "100%" }} type='file' name='file' onChange={handleChange} />
        {
           errorMessage==="Profile photo is required." ? <Typography style={{color:"red"}} textAlign={"left"}>{errorMessage}</Typography>: null
        } 
        </div>
        </Stack>
        </>
  
  )
}
