
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import AdbIcon from '@mui/icons-material/Adb';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useTheme } from '@mui/material/styles';
import image from "../../images/FindMyPik-Logo.svg"

import { useEffect, useState } from 'react';
import { AccountCircle, CameraAltRounded, Notifications, PersonAdd, Search } from '@mui/icons-material';
import { useLocation, useNavigate } from 'react-router';
import SerachField from '../component/SerachField';
import { Grid, Select, Stack } from '@mui/material';
import { makeStyles } from '@mui/styles';


const pages = ['Products', 'Pricing', 'Blog'];
const settings = ['Logout',"Profile"];

const useStyles = makeStyles((theme) => ({
  select: {
    '&:focus': {
      backgroundColor: 'transparent', // Remove focus background color
    },
    '& .MuiOutlinedInput-notchedOutline': {
      border: 'none', // Remove the border
    },
  },
}));


function Header({sidebarOpen}) {
  const classes = useStyles();
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElUser, setAnchorElUser] = useState(null);
  const navigate=useNavigate()
  const theme = useTheme();
  const isMobileOrTablet = useMediaQuery(theme.breakpoints.down('sm'));
  const location = useLocation();


  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
const handleMouseLeave =()=>{
  console.log("leave mouse");
  setOpen(false);
}

  
  const handleOpenNavMenu = (event) => {
    // setAnchorElNav(event.currentTarget);
    sidebarOpen();
  };
  const handleOpenUserMenu = (event) => {
    //console.log("event",event)
    setAnchorElUser(event.currentTarget);
  };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

  const handleCloseUserMenu = () => {
    setAnchorElUser(null);
  };

  const clearLocalStorage = () => {
    localStorage.clear();
    navigate("/")
    
 };
const [targetValue,setTargetValue]=useState('');
const handleChange=(data)=>{
    console.log("data",data);
  if(data === "myAlbum"){
    navigate("/")
  }else if(data === "public"){
    navigate('/publicGroup')
  }else if(data === "Joined"){
    navigate("/joinedAlbum")
  }else{
    console.log("nothing");
  }
}

const handleValueChange = (e)=>{
    const data = e.target.value

    if(data === "My Albums"){
      navigate("/")
    }else if(data === "Public Albums"){
      navigate('/publicGroup');
    }else if(data === "Joined Albums"){
      navigate("/joinedAlbum")
    }else{
      console.log("Nothing");
    }
}
const loc = location.pathname;
useEffect(()=>{
  console.log("hhhhhhh",loc);
  if(loc === "/"){
    setTargetValue("My Albums")
  }else if(loc === "/publicGroup"){
    setTargetValue("Public Albums")
  }else if(loc === "/joinedAlbum"){
    setTargetValue("Joined Albums")
  }else{
    setTargetValue("")
  }
},[loc])

const profilePage = () => {
  navigate("/profile")
  setAnchorElUser(null);
  
};

  return (
    <AppBar position="fixed" sx={{height:"60px",backgroundColor:"white"}}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          {/* <CameraAltRounded sx={{ display: { xs: 'none', md: 'flex' }, mr: 1 ,color:"#1976D2" }}/> */}
          {/* <Typography
            variant="h6"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: 'none', md: 'flex' },
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
              color: '#1976D2',
              textDecoration: 'none',
             
            }}
          >
            Find My Pik
          </Typography> */}

       <img src={image}/>

          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
         

            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
               onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
            </Menu>
            
          </Box>
          <Typography
            variant="h5"
            noWrap
            component="a"
            href="#app-bar-with-responsive-menu"
            sx={{
              mr: 2,
              display: { xs: 'flex', md: 'none' },
              flexGrow: 1,
              fontFamily: 'monospace',
              fontWeight: 700,
              letterSpacing: '.3rem',
            
              color: '#1976D2',
              textDecoration: 'none',
            }}
          >
           Find My Pik
          </Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
          </Box>
<Grid sx={{
  spacing: 2,
  display: { xs: 'none', md: 'flex' },
  flexDirection: 'row'
}}>
<div style={{ borderBottom: targetValue === "My Albums" ? '1px solid #1976D2' : null,display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
  <Button sx={{ color: '#1976D2', borderRadius: 10, textTransform: 'none', mr: 2 }} onClick={() => handleChange("myAlbum")}>My Albums</Button>
</div>


<div style={{ borderBottom: targetValue === "Public Albums" ? '1px solid #1976D2' : null,display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center'  }}>
<Button sx={{  color: '#1976D2',borderRadius:10,textTransform:'none',mr: 2 }} onClick={() => handleChange("public")}>Public Albums</Button>

</div>
<div style={{ borderBottom: targetValue === "Joined Albums" ? '1px solid #1976D2' : null,display: 'flex', flexDirection: 'row', justifyContent: 'center', alignItems: 'center' }}>
<Button sx={{  color: '#1976D2',borderRadius:10,textTransform:'none',mr: 2 }} onClick={() => handleChange("Joined")}>Private Albums</Button>
</div>
</Grid>


   <Stack spacing={1} sx={{ display: { xs: 'flex', md: 'none' },}}  onMouseLeave={handleMouseLeave}>
         <Select
    className={classes.select}
  value={targetValue}
  onChange={handleValueChange}
  sx={{ color: 'white' }}
  open={open}
  onClose={handleClose}
  onOpen={handleOpen}
  onMouseEnter={handleOpen}

>
  <MenuItem value={"My Albums"}>My Albums</MenuItem>
  <MenuItem value={"Joined Albums"}>Joined Albums</MenuItem>
  <MenuItem value={"Public Albums"}>Public Albums</MenuItem>
</Select>

        </Stack>
        
          <Box sx={{ flexGrow: 0 }}>
            
            <Tooltip title="Open settings">
              <IconButton onClick={handleOpenUserMenu} sx={{ p: 0 }}>
                {/* <Avatar alt="Remy Sharp" src={}/> */}
                <AccountCircle fontSize={isMobileOrTablet?"medium": "large"}style={{ color: '#1976D2' }} />
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: '45px' }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'right',
              }}
              open={Boolean(anchorElUser)}
              onClose={handleCloseUserMenu}
            >
              {settings.map((setting) => (
                <MenuItem key={setting} onClick={setting === 'Logout' ? clearLocalStorage : profilePage} style={{width:200}}>
                  <Typography textAlign="center">{setting}</Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default Header;