import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router";

import { DownloadImage, getAllImages } from "../API/Photographer";
import { Button, IconButton, Modal } from "@mui/material";
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import ArrowBackIosNewIcon from '@mui/icons-material/ArrowBackIosNew';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import '../css/view.css';
import { Clear } from "@mui/icons-material";

function ViewImages({deleteData,closeDeleteImage}) {
  const location = useLocation();
  const eventData = deleteData;
  const [currentPage, setCurrentPage] = useState(eventData?.currentPage);
  const [event, setEvent] = useState(eventData.filename);
  const [activeImage, setActiveImage] = useState(eventData?.Url);
  const [images, setImages] = useState([]);
  const [totalPage, setTotalPage] = useState(0);
  const [imagesPerPage, setImagesPerPage] = useState(10);
  const [availImage,setAvailImage]= useState('');
  const [originalName,setOriginalName]=useState(eventData.originalName);

  const navigate=useNavigate()


  useEffect(() => {
    getAllCarouselImages();
  }, [currentPage]);

  const getAllCarouselImages = async () => {
    try {
      const status = "allPhotos";
      const response = await getAllImages(status, currentPage, event);
      const newImages = response?.data?.data?.uploads || [];
      console.log(response.data.Pages);
      setImages(newImages);
      setTotalPage(response.data.Pages.totalPage);
      setAvailImage(response.data.Pages.imagessofthePage);
      
    } catch (err) {
      console.error("Error fetching images:", err);
    }
  };

  const handlePrevious = async() => {
    const currentIndex = images.findIndex((image) => image.fileUrl === activeImage);
    if (currentIndex > 0) {
      setActiveImage(images[currentIndex - 1].fileUrl);
      setOriginalName(images[currentIndex - 1].fileName);
    } else if (currentPage === 1 && currentIndex === 0) {
      setCurrentPage(totalPage);
      const response = await getAllImages("allPhotos", totalPage, event);
      const newImages = response?.data?.data?.uploads || [];
      setImages(newImages);
      setActiveImage(newImages[newImages.length - 1]?.fileUrl);
      setOriginalName(newImages[newImages.length - 1]?.fileName);
      setAvailImage(response.data.Pages.imagessofthePage);
    } else if(currentPage > 1 && currentIndex === 0){
      setCurrentPage(currentPage - 1);
      const response = await getAllImages("allPhotos", currentPage - 1, event);
      const newImages = response?.data?.data?.uploads || [];
      setImages(newImages);
      setActiveImage(newImages[newImages.length - 1]?.fileUrl);
      setOriginalName(newImages[newImages.length - 1]?.fileName);
      setAvailImage(response.data.Pages.imagessofthePage);
    }
  };
  

  const handleForward = async () => {
    const currentIndex = images.findIndex((image) => image.fileUrl === activeImage);
    
    if (currentIndex < imagesPerPage - 1 && currentIndex < images.length - 1) {
      setActiveImage(images[currentIndex + 1].fileUrl);
      setOriginalName(images[currentIndex + 1]?.fileName);
      console.log(images[currentIndex + 1]?.fileName);
    } else if (currentPage < totalPage) {
      setCurrentPage((prevPage) => prevPage + 1);
      const response = await getAllImages("allPhotos", currentPage + 1, event);
      const newImages = response?.data?.data?.uploads || [];
      setImages(newImages);
      setActiveImage(newImages[0]?.fileUrl);
      setOriginalName(newImages[0]?.fileName);
      setAvailImage(response.data.Pages.imagessofthePage);
    }else if(currentPage === totalPage){
        setCurrentPage(1);
        const response = await getAllImages("allPhotos", 1, event);
        const newImages = response?.data?.data?.uploads || [];
        setImages(newImages);
        setActiveImage(newImages[0]?.fileUrl);
        setOriginalName(newImages[0]?.fileName);
        setAvailImage(response.data.Pages.imagessofthePage);
        console.log(newImages[0]?.fileName);
    }
  };

  const handleDownload = async (datas) => {
    try {
      const urlss = datas.split('/');
     
      const lastTwoIndices = urlss.slice(-2);
      let uri = lastTwoIndices.join('/');
      console.log("kkkkkkkkk",uri);
      let modifiedUri = uri;
      if (uri.includes('%20')) {
        modifiedUri = uri.replace(/%20/g, ' ');
      }
      console.log("urlssurlss", modifiedUri);
      const data = {
        key: modifiedUri
      };
      const res = await DownloadImage(data);
      //console.log("response", res);
      const fileName = data.key;
      const imageData = new Uint8Array(res.data.Body.data);
      const contentType = res.ContentType;
      const imageBlob = new Blob([imageData.buffer], { type: contentType });

      const url = window.URL.createObjectURL(imageBlob);
      const link = document.createElement('a');
      link.href = url;
      link.setAttribute('download', fileName);
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (err) {
      //console.log(err);
    }
  };

const uploadImages=()=>{
  closeDeleteImage(false)
}

  return (
    <Modal open={true}>

   
    <div style={{ 
        position: "relative",
        backgroundColor: "black", 
        height: "88.5vh", 
        display: "flex", 
        flexDirection: "column" ,
        justifyContent: "space-between", 
        alignItems: "center", 
        marginTop:65,
        marginInline:5
      }}>

    <Clear className="view-Cancel" sx={{fontSize:'30px'}} onClick={() => uploadImages()} />

        <ArrowBackIosNewIcon 
          onClick={handlePrevious}
          sx={{ position: "absolute", left: 0, top: "50%", transform: "translateY(-50%)",color:'white' }}/>

        <div style={{ 
          position: "relative",
          backgroundColor: "black", 
          height: "85vh", 
          display: "flex", 
          flexDirection: "row" ,
          justifyContent: "space-between", 
          alignItems: "center", 
          marginTop:10
        }}>
        <img 
          src={activeImage} 
          alt="images" 
          style={{ maxWidth: "100%", maxHeight: "85vh" }} 
        />
    
  <ArrowCircleDownIcon className="view-Download"  sx={{fontSize:'50px'}} onClick={() => handleDownload(activeImage)}/>
 
</div>

       
        <ArrowForwardIosIcon 
          onClick={handleForward} 
          sx={{ position: "absolute", right: 0, top: "50%", transform: "translateY(-50%)",color:'white' }}/>

      
      </div>
      </Modal>
      
  );
}  

export default ViewImages;
